
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
ViewBox,
XHeader,
Swiper,
Marquee,
MarqueeItem,
Scroller
},
data () {
return {
imglist: [], // 
informList: [], // 
index: 0,
minAmount: '',
maxAmount: '',
creditTerm: '',
pulldownConfig: { // 
content: '',
height: 60,
autoRefresh: true,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullupConfig: { // 
content: 'Pull Up To Refresh',
pullUpHeight: 60,
height: 40,
autoRefresh: true,
downContent: '--',
upContent: '--',
loadingContent: '--',
clsPrefix: ''
}
}
},
mounted () {

this.getBaseInfo() // 
this.getPicList() // 

this.init() // 
},
activated () {
},
methods: {
onPulldownLoading () {

let self = this

self.getPicList()

self.$nextTick(() => {


})
},
toInform () {

if (this.isLogin()) {
if (this.isVerfied()) {
this.$router.push('/inform')
} else {
this.$vux.toast.text('')
}
} else {
this.$vux.toast.text('')
}
},
jumpTo (page) {

if (this.isLoginAndVerfied()) {
switch (page) {
case 'inform': this.toInform()
break
case 'goApply': this.goApply()
break
case 'BankCardList': this.$router.push('/bankCardList')
break
case 'MyLoan': this.goLoan()
break
case 'MyRepayment': this.$router.push('/repaymentRecord')
break
case 'MyGuarantee': this.$router.push('/myGurantee')
break
}
}
},
goLoan () {
this.$router.push('/checkLoan')
this.$store.state.tabItem = 1
},
guid () {
this.$router.push('/guid')
},
unOpen () {

this.$vux.toast.show({
type: 'text',
text: ','
})
},
getBaseInfo () {

let self = this
let data = {
action: 'get_request'
}

self.$store.dispatch('normalRequest', data).then(res => {

self.$store.commit('INIT_USER_INFO', res)
self.applyInfo()
})
},
getPicList () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/homePageInit.do'
let data = {
action: 'init_request',
path: path
}

self.$store.dispatch('initRequest', data).then(data => {




let arr = []

if (data.response === 'success') {
self.imglist = []
self.minAmount = data.data.minAmount
self.maxAmount = data.data.maxAmount
self.creditTerm = data.data.creditTerm
self.informList = data.data.noticeList
arr = data.data.carouselList
arr.forEach((element) => {
self.imglist.push({img: self.$store.state.baseUrl + '/app/fileUpload/showImage?id=' + element})
})
} else {
self.$vux.toast.text(data.data)
}
})
},
init () {
let self = this

window.toLogin = (res) => {
self.getUserInfo()
self.$router.replace('/login')
}
window.setUserInfo = (res) => {
self.getUserInfo()
self.$router.replace('/setting')
}
},
goApply () {
if (this.$store.state.canApply === 1) { // 1 2
this.$router.push('/loan')

} else {

switch (this.$store.state.applyState) {
case 0: this.applyInfo() // 
break
case 1: this.$router.push('/quotaEvaluation') // 
break
case 2: this.$router.push('/fail') // 
break
case 3: this.$router.push('/sign') // 
break
case 4: this.$router.push('/sign') // 
break
case 5: this.$vux.toast.text(',') // 
break
case 6: this.$router.push('/fail') // 
break
}
}
},
applyInfo () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/applyPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(data => {
self.$store.state.canApply = data.data.canApply
self.$store.state.applyNo = data.data.applyNo
switch (data.data.explain) {
case '1': self.$store.state.applyState = 1; self.page = '/quotaEvaluation' // 
break
case '2': self.$store.state.applyState = 2; self.page = '/fail' // 
break
case '3': self.$store.state.applyState = 3; self.page = '/sign' // 
break
case '4': self.$store.state.applyState = 4; self.page = '/sign' // 
break
case '5': self.$store.state.applyState = 5; self.page = '/success' // 
break
case '6': self.$store.state.applyState = 6; self.page = '/fail' // 
break
}
})
},
isLoginAndVerfied () {
if (this.isLogin()) {
if (this.isVerfied()) {
return true
} else {

this.toVerfiedPage()
}
} else {
this.$router.push('/login')
}
}
}
}



